<template>
  <div>
    <p v-if="errorMessage">Error: {{ errorMessage }}</p>
    <p v-else-if="loading">Processing login...</p>
    <p v-else>Welcome, {{ userName }}!</p>
  </div>
</template>

<script>
import { useToast } from "vue-toastification";

export default {
  name: "KakaoCallback",
  data() {
    return {
      loading: true,
      errorMessage: null,
      userName: null,
    };
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  mounted() {
    const code = new URLSearchParams(window.location.search).get("code");
    if (!code) {
      this.errorMessage = "Authorization code not found.";
      this.loading = false;
      this.toast.error(
        "Î°úÍ∑∏Ïù∏ÏùÑ ÏúÑÌïú Ïù∏Ï¶ù ÏΩîÎìúÍ∞Ä ÏóÜÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî."
      );
      return;
    }

    if (!window.Kakao.isInitialized()) {
      window.Kakao.init(process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY);
      console.log("Kakao SDK initialized");
    }

    this.handleKakaoLogin(code);
  },
  methods: {
    async handleKakaoLogin(code) {
      try {
        const response = await fetch("https://kauth.kakao.com/oauth/token", {
          method: "POST",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          body: new URLSearchParams({
            grant_type: "authorization_code",
            client_id: process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY,
            redirect_uri: process.env.VUE_APP_KAKAO_REDIRECT_URI,
            code,
          }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();

        if (data.error) {
          throw new Error(data.error_description || "Token error");
        }

        window.Kakao.Auth.setAccessToken(data.access_token);
        console.log("Access Token Set:", data.access_token);

        this.fetchUserInfo();
      } catch (err) {
        // ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò ÎòêÎäî HTTP ÏÉÅÌÉú ÏΩîÎìúÏóê Îî∞Î•∏ Ï≤òÎ¶¨
        console.error("Token Request Error:", err);
        if (err.message.includes("Failed to fetch")) {
          this.toast.error("ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò: Ïù∏ÌÑ∞ÎÑ∑ Ïó∞Í≤∞ÏùÑ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
        } else if (err.message.includes("HTTP error")) {
          this.toast.error(`ÏÑúÎ≤Ñ Ïò§Î•ò Î∞úÏÉù: ${err.message}`);
        } else if (err.message.includes("invalid_grant")) {
          this.toast.error(
            "Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ïù∏Ï¶ù ÏΩîÎìúÏûÖÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî."
          );
        } else {
          this.toast.error(
            "Î°úÍ∑∏Ïù∏ ÏöîÏ≤≠ Ï§ë Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî."
          );
        }
        this.errorMessage = "Login failed due to a network or server error.";
        this.loading = false;
      }
    },
    fetchUserInfo() {
      window.Kakao.API.request({
        url: "/v2/user/me",
        success: (response) => {
          console.log("User Info Response:", response);

          if (
            response.kakao_account &&
            response.kakao_account.profile &&
            response.kakao_account.profile.nickname
          ) {
            this.userName = response.kakao_account.profile.nickname;

            // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï†ÄÏû•
            localStorage.setItem("kakao_user", JSON.stringify(response));

            // Î∂ÄÎ™® Ïª¥Ìè¨ÎÑåÌä∏Ïóê ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï†ÑÎã¨
            this.$emit("login-success", response);

            // ÌôàÏúºÎ°ú Ïù¥Îèô
            this.$router.push("/home");
            this.toast.success(`Welcome, ${this.userName}!`);
          } else {
            console.error("User profile data is not available.");
            this.errorMessage = "Profile information is missing.";
            this.toast.error("ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
            this.loading = false;
          }
        },
        fail: (error) => {
          console.error("Failed to fetch user info:", error);
          this.toast.error("ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
          this.errorMessage = "Failed to fetch user info.";
          this.loading = false;
        },
      });
    },
  },
};
</script>
