<template>
  <div class="login-container">
    <!-- Î≥Ñ Ïï†ÎãàÎ©îÏù¥ÏÖò -->
    <div class="star-field">
      <div class="star" v-for="n in 50" :key="n"></div>
    </div>

    <div class="login-box">
      <h1 class="login-title">Ïπ¥Ïπ¥Ïò§ ÏÜåÏÖú Î°úÍ∑∏Ïù∏</h1>
      <p class="login-description">Ïπ¥Ïπ¥Ïò§ Í≥ÑÏ†ïÏúºÎ°ú Î°úÍ∑∏Ïù∏ÌïòÏÑ∏Ïöî.</p>
      <!-- Ïπ¥Ïπ¥Ïò§ Î°úÍ∑∏Ïù∏ Ïù¥ÎØ∏ÏßÄ Î≤ÑÌäº -->
      <img
        :src="kakaoButtonImage"
        alt="Kakao Login"
        class="login-button"
        @click="handleKakaoLogin"
        :class="{ disabled: loading }"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "SignIn",
  data() {
    return {
      loading: false,
      kakaoButtonImage: require("@/assets/kakaologin.png"), // Ïù¥ÎØ∏ÏßÄ Í≤ΩÎ°ú ÏÑ§Ï†ï
    };
  },
  methods: {
    handleKakaoLogin() {
      if (
        !process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY ||
        !process.env.VUE_APP_KAKAO_REDIRECT_URI
      ) {
        console.error("Kakao JavaScript Key or Redirect URI is not defined.");
        alert("ÌôòÍ≤Ω Î≥ÄÏàò ÏÑ§Ï†ï Ïò§Î•ò: Í¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌïòÏÑ∏Ïöî.");
        return;
      }

      if (!window.Kakao.isInitialized()) {
        try {
          window.Kakao.init(process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY);
          console.log("Kakao SDK initialized successfully.");
        } catch (error) {
          console.error("Kakao SDK initialization failed:", error);
          alert("Kakao SDK Ï¥àÍ∏∞Ìôî Ïã§Ìå®. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
          return;
        }
      }

      this.loading = true;

      try {
        console.log("Starting Kakao login process...");
        window.Kakao.Auth.authorize({
          redirectUri: process.env.VUE_APP_KAKAO_REDIRECT_URI,
        });
      } catch (error) {
        console.error("Kakao authorization failed:", error);
        alert("Î°úÍ∑∏Ïù∏ ÏöîÏ≤≠ Ïã§Ìå®. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
        this.loading = false;
      }
    },
  },
};
</script>

